import React, { PureComponent } from 'react'
import { Table, Modal, message } from 'antd'
const confirm = Modal.confirm
import { connect } from 'react-redux'
import { mapDispatchToProps } from '../redux/mapDispatchToProps'
import { countTableScroll } from 'util'
import { withRouter } from 'react-router'
@connect(
  state => ({ ...state.generalBomSetting, userLoation: state.userLoation }),
  mapDispatchToProps
)
class AppTable extends PureComponent {
  constructor(props) {
    super(props)
    this.state = {}
    this.columns = [
      {
        title: '地点',
        width: 220,
        dataIndex: 'x',
        key: 'x',
        render: (text, record) => {
          const { locationCode = '', locationName = '' } = record
          return (
            `${locationCode} ${locationName}`
          )
        }
      },
      {
        title: '产线编码',
        width: 80,
        dataIndex: 'lineCode',
        key: 'lineCode'
      },
      {
        title: '产线名称',
        width: 180,
        dataIndex: 'lineName',
        key: 'lineName'
      },
      {
        title: '商品大类',
        width: 100,
        dataIndex: 'largeCategoryName',
        key: 'largeCategoryName'
      },
      {
        title: '商品中类',
        width: 100,
        dataIndex: 'middleCategoryName',
        key: 'middleCategoryName'
      },
      {
        title: '商品小类',
        width: 100,
        dataIndex: 'smallCategoryName',
        key: 'smallCategoryName'
      },
      {
        title: '数量因子',
        width: 100,
        dataIndex: 'reckonFactor',
        key: 'reckonFactor'
      },
      {
        title: '机时',
        width: 60,
        dataIndex: 'machineFee',
        key: 'machineFee',
        render: (text, record) => {
          const { machineFeeUnit = '' } = record
          const unit = machineFeeUnit == 1 ? 'H' : machineFeeUnit == 2 ? '元' : ''
          return `${text} ${unit}`
        }
      },
      {
        title: '工时',
        width: 60,
        dataIndex: 'workerFee',
        key: 'workerFee',
        render: (text, record) => {
          const { workerFeeUnit = '' } = record
          const unit = workerFeeUnit == 1 ? 'H' : workerFeeUnit == 2 ? '元' : ''
          return `${text} ${unit}`
        }
      },
      {
        title: '辅料',
        width: 60,
        dataIndex: 'supportMaterialFee',
        key: 'supportMaterialFee',
        render: (text, record) => {
          const { supportMaterialFeeUnit = '' } = record
          const unit = supportMaterialFeeUnit == 1 ? 'H' : supportMaterialFeeUnit == 2 ? '元' : ''
          return `${text} ${unit}`
        }
      },
      {
        title: '状态',
        width: 60,
        dataIndex: 'status',
        key: 'status',
        render: (text, record, index) =>
          text == 1 ? '启用' : text == 0 ? '禁用' : '--'
      },
      {
        title: '最后更新时间',
        width: 150,
        dataIndex: 'updateTime',
        key: 'updateTime'
      },
      {
        title: '更新人员',
        width: 80,
        dataIndex: 'updateBy',
        key: 'updateBy'
      },
      {
        title: '操作',
        width: 150,
        dataIndex: 'xx',
        key: 'xx',
        fixed: 'right',
        render: (text, record, index) => (
            <div className='tableOperation'>
               {this.renderBtn(text, record, index)}
            </div>
          )
      }
    ]
  }

  renderBtn(text, record, index) {
    const { status } = record
    const { menuPermissionIds } = this.props
    const flag = menuPermissionIds && menuPermissionIds.includes('generalBomSetting_forbiddenOrAllow') || false
    const flag_editInTable = menuPermissionIds && menuPermissionIds.includes('generalBomSetting_editInTable') ? 'allowEdit' : 'forbiddenEdit'
    let dom = <div>
              <span
                className= { flag ? 'blue' : 'disabledBtnInTable'}
                onClick={() => {
                  this.handleOperator(text, record, index)
                }}
              >
                {status == 0 ? '启用' : status == 1 ? '禁用' : ''}
              </span>
              <span
                className='blue'
                onClick={this.linkToDet.bind(this, record, flag_editInTable)}
              >
                 { flag_editInTable === 'allowEdit' ? '编辑' : '查看'}
              </span>
    </div>
    return dom
  }
  handleOperator(text, record, index, type) {
    const {
      handleGetList,
      searchParams,
      page,
      size,
      handleOperator
    } = this.props
    const { status = '', id = '' } = record
    let optionStr = ''
    if (status == '1') {
      optionStr = '禁用 '
    } else if (status == '0') {
      optionStr = '启用'
    }
    confirm({
      title: '温馨提示',
      content: `是否进行${optionStr}操作？`,
      okText: '确认',
      width: 520,
      cancelText: '取消',
      onOk() {
        handleOperator(
          {
            urlParams: [id, `${status}`]
          },
          () => {
            message.success('操作成功')
            handleGetList({ searchParams: { page, size, ...searchParams } })
          }
        )
      }
    })
  }
  linkToDet = (record, flag_editInTable) => {
    const { history } = this.props
    history.push(
      `/mms/settingCenter/productManage/generalBomSetting/det/${record.id}?editOrCreate=edit&permissionFlag=${flag_editInTable}`
    )
  }
  componentDidMount() {
    const { handleGetList, searchParams, page, size } = this.props
    handleGetList({ searchParams: { page, size, ...searchParams } })
  }
 // 监听下拉仓库改变了重新获取数据
  componentWillReceiveProps(newProps, newStates) {
    const { handleGetList, userLoation } = this.props
    const newUserLoationValue = newProps && newProps.userLoation && newProps.userLoation.value || ''
    const userLoationValue = userLoation && userLoation.value || ''
    if (newUserLoationValue != userLoationValue) {
      handleGetList({ searchParams: { page: 1, size: newProps.size, ...newProps.searchParams } })
    }
  }
  onChange = (pagination, filters, sorter, extra) => {
    const { handleGetList, searchParams, page, size } = this.props
    if (sorter.order) {
      if (sorter.order === 'ascend') {
        handleGetList({
          searchParams: {
            page,
            size,
            ...searchParams,
            isAsc: true,
            columns: sorter.field
          }
        })
      } else {
        handleGetList({
          searchParams: {
            page,
            size,
            ...searchParams,
            isAsc: false,
            columns: sorter.field
          }
        })
      }
    } else {
      // 判断是点跳页面还是排序
      let reqObj = { ...searchParams }
      for (let i in reqObj) {
        if (Object.prototype.toString.call(reqObj[i]) === '[object Object]') {
          reqObj[i] = reqObj[i].value
        }
      }
      handleGetList({
        searchParams: {
          page: pagination.current,
          size: pagination.pageSize,
          ...reqObj
        }
      })
    }
  }
  render() {
    const {
      total,
      page,
      size,
      tableLoading,
      selectedRowKeys,
      searchResult = []
    } = this.props
    let pagination = {
      total,
      showTotal: (total, range) => `共 ${total} 条`, // 显示总数
      showQuickJumper: true, // 是否可以快速跳转至某页
      current: page,
      pageSize: size,
      showSizeChanger: true, // 是否可以改变 pageSize
      className: 'pagination',
      pageSizeOptions: ['10', '50', '100', '200', '500']
    }
    let rowSelection = {
      selectedRowKeys,
      onChange: (selectedRowKeys, selectedRows) => {
        const { update } = this.props
        update({ selectedRowKeys, selectedRows })
      }
    }
    return (
      <div className='table-group'>
        <Table
          rowKey={record => record.id}
          loading={tableLoading}
          columns={this.columns}
          dataSource={searchResult}
          scroll={{ x: countTableScroll(this.columns) }}
          onChange={this.onChange}
          pagination={pagination}
          rowSelection={rowSelection}
        />
      </div>
    )
  }
}
export default withRouter(AppTable)



