import React, { PureComponent } from 'react'
import { Form, Button, Row, Col } from 'antd'
import style from './style/Form.scss'
import { connect } from 'react-redux'
import { mapDispatchToProps } from '../redux/mapDispatchToProps'
import { CsxSelect } from 'Form'
import {
  API_GET_LOCATION_CODEANDNAME,
  API_GET_CATEGORY_PRO_CODEANDNAME,
  API_PRODUCTIONFREQUENCYS_PRODUCTIONLINE_LIST
} from 'api'
import { withRouter } from 'react-router'
@connect(
  state => ({ ...state.generalBomSetting }),
  mapDispatchToProps
)
class AppForm extends PureComponent {
  constructor() {
    super(...arguments)
  }
  // 改变当前的输入值
  onInputChange(values) {
    const { searchParams, update } = this.props
    update({ searchParams: { ...searchParams, ...values } })
  }
  // 查询数据
  query = () => {
    const { handleGetList, searchParams, size } = this.props
    let reqObj = { ...searchParams }
    console.log('searchParams is', searchParams)
    console.log('reqObj is', reqObj)
    for (let i in reqObj) {
      if (Object.prototype.toString.call(reqObj[i]) === '[object Object]') {
        reqObj[i] = reqObj[i].value
      }
    }
    console.log('reqObj is', reqObj)
    handleGetList({ searchParams: { ...reqObj, page: 1, size } })
  }
  // 重置
  reset = () => {
    const { update } = this.props
    update({ searchParams: {}, productCodeOptions: {} })
  }
  render() {
    const { searchParams = {} } = this.props
    const {
      locationCode,
      largeCategoryCode,
      middleCategoryCode,
      smallCategoryCode,
      lineCode } = searchParams

    return (
      <Form className={style.form}>
        <Row type='flex' justify='start'>
          <Col span={8}>
            <CsxSelect
              dateApi={API_GET_LOCATION_CODEANDNAME}
              label='地点编码/名称'
              allowClear={true}
              selectItem={{
                data: locationCode,
                key: 'code',
                valueKey: ['code', 'name']
              }}
              onChangeSelect={value => {
                this.onInputChange({ locationCode: value })
              }}
            />
          </Col>
          <Col span={8}>
            <CsxSelect
              dateApi={API_PRODUCTIONFREQUENCYS_PRODUCTIONLINE_LIST}
              label='产线'
              allowClear={true}

              selectItem={{
                data: lineCode,
                key: 'code',
                valueKey: ['code', 'name']
              }}
              onChangeSelect={value => {
                this.onInputChange({ lineCode: value })
              }}
            />
          </Col>
          <Col span={18}>
            <Row type="flex">
              <CsxSelect
                dateApi={API_GET_CATEGORY_PRO_CODEANDNAME}
                allowClear={true}
                label='商品分类'
                disabled={locationCode ? false : true}
                placeholder='大类'
                searchParams={{ identifying: '1', locationCode: locationCode && locationCode.value ? locationCode.value : '' }}
                selectItem={{
                  data: largeCategoryCode,
                  key: 'code',
                  valueKey: ['code', 'name']
                }}
                onChangeSelect={(value) => {
                  this.onInputChange({ largeCategoryCode: value, middleCategoryCode: '', smallCategoryCode: '' })
                  // this.props.form.setFieldsValue({
                  //   smallCategory: '',
                  //   middleCategory: ''
                  // })
                }}
                style={{ width: 163 }}
              />
              <CsxSelect
                placeholder='中类'
                dateApi={API_GET_CATEGORY_PRO_CODEANDNAME}
                allowClear={true}
                disabled={
                  locationCode ?
                  largeCategoryCode && largeCategoryCode.value ? false : true
                  : true
                }
                searchParams={
                  largeCategoryCode && largeCategoryCode.value ? {
                    categoryCode: largeCategoryCode && largeCategoryCode.value,
                    identifying: '2',
                    locationCode: locationCode && locationCode.value ? locationCode.value : ''
                  } : {
                    identifying: '2'
                  }}
                selectItem={{
                  data: middleCategoryCode,
                  key: 'code',
                  valueKey: ['code', 'name']
                }}
                onChangeSelect={(value) => {
                  this.onInputChange({ middleCategoryCode: value, smallCategoryCode: '' })
                }}
                errMessage='请先选择大类'
                style={{ marginLeft: 10, width: 163 }}
              />
              <CsxSelect
                placeholder='小类'
                allowClear={true}
                dateApi={API_GET_CATEGORY_PRO_CODEANDNAME}
                disabled={
                  locationCode ?
                  middleCategoryCode && middleCategoryCode.value ? false : true
                  : true
                }
                searchParams={
                  largeCategoryCode && largeCategoryCode.value ? {
                    categoryCode: middleCategoryCode && middleCategoryCode.value,
                    identifying: '3',
                    locationCode: locationCode && locationCode.value ? locationCode.value : ''
                  } : {
                    identifying: '3'
                  }}
                selectItem={{
                  data: smallCategoryCode,
                  key: 'code',
                  valueKey: ['code', 'name']
                }}
                onChangeSelect={(value) => {
                  this.onInputChange({ smallCategoryCode: value })
                }}
                errMessage='请先选择中类'
                style={{ marginLeft: 10, width: 163 }}
              />
            </Row>
          </Col>
          <Col span={24}>
            <Row type='flex' justify='end'>
              <Button
                type='primary'
                className={style.mr10}
                onClick={this.query}
              >
                查询
              </Button>
              <Button type='primary' onClick={this.reset}>
                重置
              </Button>
            </Row>
          </Col>
        </Row>
      </Form>
    )
  }
}
export default Form.create()(withRouter(AppForm))



