import React, { PureComponent } from 'react'
import { message, Modal, Button } from 'antd'
import {} from 'api'
import style from './style/Tool.scss'
import { connect } from 'react-redux'
import { withRouter } from 'react-router'
import { mapDispatchToProps } from '../redux/mapDispatchToProps'
const confirm = Modal.confirm
@connect(
  state => ({ ...state.generalBomSetting }),
  mapDispatchToProps
)
class TableTool extends PureComponent {
  constructor(props) {
    super(props)
    this.state = {}
  }
  // 新增
  handleAdd = () => {
    const { history } = this.props
    history.push(
      '/mms/settingCenter/productManage/generalBomSetting/create?editOrCreate=create&permissionFlag=allowEdit'
    )
  }
  // 删除
  handleOperator = () => {
    const {
      handleGetList,
      searchParams,
      page,
      size,
      selectedRowKeys,
      handleDel,
      update
    } = this.props
    if (!(selectedRowKeys && selectedRowKeys.length > 0)) {
      return message.error('没有可以操作的选项')
    }
    confirm({
      title: '温馨提示',
      content: '是否进行删除操作？',
      okText: '确认',
      width: 520,
      cancelText: '取消',
      onOk() {
        handleDel(
          {
            data: selectedRowKeys
          },
          () => {
            update({ selectedRowKeys: [] })
            message.success('操作成功')
            handleGetList({ searchParams: { page, size, ...searchParams } })
          }
        )
      }
    })
  }
  render() {
    const { menuPermissionIds = [] } = this.props
    return (
      <div className={style['table-tool']}>
        {menuPermissionIds &&
          menuPermissionIds.includes('generalBomSetting_add') && (
            <Button
              type='primary'
              className={style.btn}
              onClick={this.handleAdd}
            >
              新增
            </Button>
          )}
        {menuPermissionIds && menuPermissionIds.includes('generalBomSetting_delete') && (
          <Button
            type='primary'
            className={style.btn}
            onClick={this.handleOperator}
          >
            删除
          </Button>
        )}
      </div>
    )
  }
}
export default withRouter(TableTool)



